body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.Content{display:grid;border:1px solid grey;background-image:url("https://static.wixstatic.com/media/c879fd_6eab8d37869d4af585f80583f9be181d~mv2_d_3000_1708_s_2.jpg/v1/fill/w_1920,h_1093,al_c,q_85,usm_0.66_1.00_0.01/c879fd_6eab8d37869d4af585f80583f9be181d~mv2_d_3000_1708_s_2.webp");background-repeat:no-repeat;background-size:cover;max-width:100%;height:auto}.Content_Mobile{background:#cce5f8}.Content_Homepage{background:#cce5f8}.Header{min-height:120px;position:relative;top:0;left:0;right:0;display:flex;justify-content:space-between;align-items:center;padding:0;z-index:100}.Logo{width:20%;float:left}.Main{margin:0  auto 30px  auto;width:60%;background:rgba(245,245,255,0.9);border-radius:2px;color:#000}.Main .Animated-Text{overflow:hidden;height:1.1em;word-wrap:break-word;white-space:nowrap;-webkit-animation:typing 6s steps(100) forwards;animation:typing 6s steps(100) forwards}@-webkit-keyframes typing{from{width:0}to{width:100ch}}@keyframes typing{from{width:0}to{width:100ch}}.Main_Mobile{margin:0  0 0  0;background:rgba(245,245,255,0.9);color:#000;padding:10px 0}.Main_Mobile .Headline{padding-left:0;text-align:left;text-align:initial;margin-top:0;font-size:1.25em}.Footer{background:#789;filter:alpha(opacity=60)}

.Logo_Image{width:150px;height:120px;margin:30px 0 0 30px}.Logo_Image_Mobile{width:80px;margin:10px}

.Footer{color:#fff;padding-top:1px}.Footer .Footer-Link-Info{margin:0 0 8px 0;font-size:small}.Footer h4{text-transform:uppercase;margin:0 0 10px 0}.Footer a{text-decoration:none;color:#fff;font-size:small}.Footer .FooterLink{margin-left:5px}.Footer .SeparateLine{border-width:0.75px}.Info_Section{margin-right:50px}.Info_Section_Tablet{margin-left:30px}.Info_Section_Mobile{margin-left:20px;margin-bottom:20px}.Info_Section_Mobile:first-child{padding-top:20px}.Contact_Daten{display:flex;width:60%;margin:40px auto}.Contact_Daten_Mobile{display:block}.Copyright{text-align:center;padding-bottom:20px;padding-top:20px;margin:0}.Telephone-Number{color:#b41527;padding-left:20px}.Footer-Hotline a{color:#b41527;font-weight:700}

.Link{font-size:small;cursor:pointer}.Margin-Bottom{margin:0 0 8px 0;font-size:small}

.ModalCover{position:fixed;z-index:10;background:white;left:0;top:0;width:100%;height:100%;-webkit-transform:translateZ(0);transform:translateZ(0);background-color:rgba(0,0,0,0.5)}.ModalCover .ModalCloseButton{padding:0;border:none;position:absolute;right:5px;top:5px}.ModalArea{padding:20px;width:500px;background:#fff;top:30%;position:fixed;left:calc(50% - 300px);box-sizing:border-box;box-shadow:1px 1px 1px #000;border-radius:2px;border:1px solid #000;border-radius:5px}.ModalArea_Mobile{width:300px;height:300px;position:absolute;left:50%;top:40%;margin-left:-150px;margin-top:-150px;background:#fff;border:1px solid #000;border-radius:5px}.ModalContent{width:90%}

.Auth{position:relative;padding:10px;color:#000000}.Auth h3{text-align:center;margin:5px;color:#b41527;font-size:1.5em}.Auth .ErrorMsg{color:red;font-size:0.85em}.Auth .Button:hover{background-color:#b41527;opacity:0.85;border:none}.Auth .Button{background-color:#b41527;opacity:1;border:none;margin-bottom:0;text-transform:none}.Auth .Button:focus{outline:none}.Auth .AuthForm div:nth-child(2){margin-top:20px}

.InputEl{outline:none;border:1px solid #ccc;background-color:white;font:inherit;padding:10px;display:block;width:100%;box-sizing:border-box;border-radius:3px}textarea{height:200px}.Label{display:inline-block;margin-bottom:5px}.InputEl[type="radio"]{width:auto;display:inline;margin-left:12px;-webkit-transform:scale(1.5);transform:scale(1.5);cursor:pointer;position:absolute;bottom:0}.InputEl:focus{border-color:dimgray}.Valid{border:1px solid #07b03c}.Invalid{border:1px solid #e40000}.Error{color:#e40000;margin:0}.CustomerSalutation{display:inline;margin-right:40px;position:relative}

.Button{background-color:#b41527;border:none;color:white;cursor:pointer;font:inherit;padding:10px;margin:10px;text-transform:uppercase;letter-spacing:0.04em;border-radius:4px;font-weight:500;font-size:0.875rem;line-height:1.75;float:right}.Primary{background-color:#5C9210}.Success{color:#5C9210}.Cancel{color:#944317}.Danger{background-color:#944317}

.Navigation{width:60%;height:56px;float:right;position:fixed;top:0;left:0;display:flex;justify-content:space-between;align-items:center;padding:0 20px;box-sizing:border-box;z-index:90}.Navigation nav{height:100%}.DesktopOnly{margin-right:60px}@media (max-width: 499px){.DesktopOnly{display:none}}

.NavigationItems{list-style:none;margin:0;padding:0;display:flex;align-items:center}

.NavigationItem{box-sizing:border-box;height:auto;width:100%;position:relative}.NavigationItem a{color:#424949;text-decoration:none;width:100%;box-sizing:border-box}.NavigationItem a:hover,.NavigationItem a:active,.NavigationItem a.active{color:#b41527}@media (min-width: 500px){.NavigationItem{align-items:center;width:auto;height:100%;margin:10px 0}.NavigationItem a{text-decoration:none;height:100%;padding:16px 10px;border-bottom:4px solid transparent;box-sizing:border-box;display:inline-block}.NavigationItem a:before{color:#b41527;-webkit-transition:all .5s ease;transition:all .5s ease;position:absolute}.NavigationItem a:before{bottom:15px;height:1.5px;width:0%;content:' ';background:#b41527;border-radius:2px}.NavigationItem a:hover:before{opacity:1;width:80%}.NavigationItem a:hover{color:#424949}.NavigationItem a.active{color:#424949}.NavigationItem a.active:before{opacity:1;width:80%}}

.DrawerToggle{width:60px;height:70%;display:flex;flex-flow:column;justify-content:space-around;align-items:center;padding:10px;box-sizing:border-box;cursor:pointer;float:right}.DrawerToggle div{width:100%;height:3px;background-color:gray;display:block;margin-bottom:8px;border-radius:1.3px}@media (min-width: 500px){.DrawerToggle{display:none}}

.SideDrawer{position:fixed;width:200px;height:100%;right:0;top:0;z-index:200;background-color:white;padding:32px 16px;box-sizing:border-box;-webkit-transition:tranform 0.3s ease-out;transition:tranform 0.3s ease-out}.SideDrawer a{color:#282c34;display:block;padding-bottom:10px;padding-top:10px}.SideDrawer nav{margin-top:35px;border-top:1px solid #f5f3f3}.SideDrawer .Kaefer_1,.SideDrawer .Kaefer_2{position:absolute;width:40px}.SideDrawer .Kaefer_1{-webkit-transform:rotate(35deg);transform:rotate(35deg);left:80px;bottom:260px}.SideDrawer .Kaefer_2{-webkit-transform:rotate(20deg);transform:rotate(20deg);left:55px;bottom:170px}.NavigationMobile ul{display:block}.NavigationMobile ul li{border-bottom:1px solid #f5f3f3;line-height:1.5rem}@media (min-width: 500px){.SideDrawer{display:none}}.Open{-webkit-animation:movedrawer normal  1s;animation:movedrawer normal  1s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes movedrawer{from{-webkit-transform:translateX(100%);transform:translateX(100%)}to{-webkit-transform:translateX(0);transform:translateX(0)}}@keyframes movedrawer{from{-webkit-transform:translateX(100%);transform:translateX(100%)}to{-webkit-transform:translateX(0);transform:translateX(0)}}.Close{-webkit-transform:translateX(100%);transform:translateX(100%);-webkit-animation:closedrawer  normal  0.8s;animation:closedrawer  normal  0.8s;-webkit-animation-fill-mode:backwards;animation-fill-mode:backwards}@-webkit-keyframes closedrawer{from{-webkit-transform:translateX(0);transform:translateX(0)}to{-webkit-transform:translateX(100%);transform:translateX(100%)}}@keyframes closedrawer{from{-webkit-transform:translateX(0);transform:translateX(0)}to{-webkit-transform:translateX(100%);transform:translateX(100%)}}

.Backdrop{position:fixed;z-index:100;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.6)}.BackdropOpen{display:block}.BackdropClosed{display:none}

.CloseButton{width:50px;height:50px;cursor:pointer;position:absolute;top:15px;right:0;z-index:1000}.CloseButton div{margin-bottom:15px;position:absolute;left:15px;content:' ';height:33px;width:3px;background-color:#333}.CloseButton div:nth-child(1){-webkit-transform:rotate(50deg);transform:rotate(50deg)}.CloseButton div:nth-child(2){-webkit-transform:rotate(-50deg);transform:rotate(-50deg)}

.Home{padding:20px}.Button{border:1px solid #521751;font:inherit;padding:8px;background-color:#521751;color:white;cursor:pointer;margin-top:30px}.Button:hover,.Button:active{background-color:#fa923f}

.MyCarousel .carousel .slide .legend{background:lightslategrey;bottom:15px;opacity:1;letter-spacing:0.75px;font-size:1em}

.Services{padding:20px}.Services p{margin:0}.Services ul{list-style:none;padding-left:20px}.Services ul li{padding-bottom:10px;padding-left:5px;position:relative}.Services svg{position:absolute;left:-20px;top:5px}.Services .ContactLink{text-decoration:none;color:#b41527;font-weight:bold}.Services .Sentence-More{margin-bottom:20px;margin-top:20px}.Headline{color:#b41527;text-transform:uppercase}.Subheadline{text-transform:none;font-weight:600}

.Contact{padding:20px}.Subheadline{color:#b41527}

.Staff_Container{height:auto;box-sizing:border-box;box-shadow:0 2px 3px #AC9980;border-radius:5px;margin:20px;position:relative;display:flex;background:#fff}.Staff_Container img{height:auto;box-shadow:1px 2px 1px 0 grey;width:150px}.StaffName{color:#b41527}.Staff_Container_Mobile{height:auto;box-sizing:border-box;box-shadow:0 2px 3px #AC9980;border-radius:5px;margin:20px 0;position:relative;display:flex;padding-bottom:15px;padding-top:15px;background:#fff}.Staff_Container_Mobile img{width:100px;height:auto;box-shadow:1px 2px 1px 0 grey}.Staff_Container_Mobile p{margin-top:5px;margin-bottom:0}.Staff_Container_Mobile .Staff_Contact{margin-left:5px}.Staff_Image{padding:10px}.Staff_Contact{float:right;width:80%;margin-left:30px}.Person_Email a{text-decoration:none;color:#000;margin-left:5px}

.ContactForm{margin:10px auto;padding:10px;box-sizing:border-box}.ContactForm h3{color:#b41527;margin-top:0;padding-left:20px}.ContactForm p{padding-left:20px}.ContactForm .Hint{font-size:small;margin-left:0;text-align:left;text-align:initial}.ContactForm .Input{display:block;margin-bottom:15px}.ContactForm .Form{padding:20px;margin-bottom:40px}.ContactForm .Button{background-color:#b41527;opacity:1}.ContactForm .Button svg{padding-left:5px}.ContactForm .Button button{float:right}.ContactForm .Button p{font-style:italic;font-size:small}.ContactForm .Button:hover{background-color:#b41527;opacity:0.85}.ContactForm_Mobile{text-align:left;text-align:initial;padding:10px;box-sizing:border-box;margin-bottom:30px}.ContactForm_Mobile .Hint{font-style:italic;font-size:small}.ContactForm_Mobile .Button{background-color:#b41527}.ContactForm_Mobile .Button svg{padding-left:5px}.ContactForm_Mobile .Subheadline{padding-left:0}.ContactForm_Mobile .Form{margin-top:25px}.CustomerSalutation{display:inline;margin-right:40px;position:relative}.CustomerSalutation input[type="radio"]{margin-right:30px;margin-left:10px;-webkit-transform:scale(1.25);transform:scale(1.25)}

.LegalNotice{padding:20px}.LegalNotice .Headline{color:#b41527;font-weight:600;text-align:left;padding:0}.LegalNotice .Subheadline{font-weight:600;margin-bottom:0;padding-left:0}.LegalNotice ul{list-style:none;padding:0}.LegalNotice .Bold{font-weight:600}.LegalNotice .MarginNull{margin:0}

.DataProtection{padding:20px}.DataProtection .Headline{color:#b41527;font-weight:600;text-align:left;padding:0;margin-bottom:0}.DataProtection .Subheadline{font-size:1.25em;color:#000000;padding-left:0}.DataProtection h4{margin:0}.DataProtection .ContactDetails{list-style:none;padding:0}.DataProtection .Bold{font-weight:600}

.CompanyAddress{padding:20px;text-align:left;clear:right}.CompanyAddress ul{list-style:none;padding:0}

.Dashboard{padding:20px}h3{color:#b41527}article>div{background:#09d3ac;height:50px;width:50px;float:left;cursor:pointer;margin:5px}

ul {
  padding:0;
  list-style:none;
 /* width: 30%; */
  margin:auto;
}

li{
  font-size:1.2rem;
  line-height: 2rem;
